.ditail-post {
    display: flex;
    justify-content: space-between; 
    img {
        max-width: 100%;
        height: auto !important;
    }
}
.author{
    max-width: 300px;
}

.container {
    max-width: 1305px;
    margin: 0 auto;
}

hr {
    border: none;
    background-color: rgba(0, 0, 0, 0.116); 
    height: 1px;
    margin: 30px 0 0;
}

.postInfo {
    padding: 0 30px;
}

@media screen and (max-width: 991px) {
    .author{
        display: none;
    }
}
